
import { computed, defineComponent, SetupContext } from "vue";

type Props = {
  currentPage: number;
  totalPage: number;
  perPage: number;
  path: string;
  paramNamePage: string;
  paramNamePerPage: string;
  displayPageCount: number;
};

export default defineComponent({
  name: "Pagination",
  props: {
    currentPage: {
      type: Number,
      required: true,
    },
    totalPage: {
      type: Number,
      required: true,
    },
    perPage: {
      type: Number,
      required: true,
    },
    path: {
      type: String,
      required: true,
    },
    paramNamePage: {
      type: String,
      required: true,
    },
    paramNamePerPage: {
      type: String,
      required: true,
    },
    displayPageCount: {
      type: Number,
      default: 3,
    },
  },
  setup(props: Props, context: SetupContext) {
    return {
      onClickPageButton: (index: number) => context.emit("page-change", index),
      hasEllipsisBegin: computed(
        () => props.currentPage - props.displayPageCount >= 3
      ),
      hasEllipsisEnd: computed(
        () => props.currentPage + props.displayPageCount <= props.totalPage - 2
      ),
    };
  },
});
